
body{
  margin:0;
  padding:0;
  background:#f1f1f1;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

h3{
    margin:50px auto;
}
.user{
    width:80%;
    height:auto;
    margin:40px auto;
    padding:20px 0;
    border:1px solid #999;
    border-radius:10px;
    background:#fff;
}
.life{
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack:distribute;
        justify-content:space-around;
}
.invite{
    width:80%;
    height:50px;
    line-height:50px;
    margin:100px auto;
    text-align:center;
    background:#fff;
    border:1px solid #999;
    border-radius:5px;
}

.live{
    overflow-x:hidden;
}

h1[data-v-2a4fcac3], h2[data-v-2a4fcac3] {
  font-weight: normal;
}
ul[data-v-2a4fcac3] {
  list-style-type: none;
  padding: 0;
}
li[data-v-2a4fcac3] {
  display: inline-block;
  margin: 0 10px;
}
a[data-v-2a4fcac3] {
  color: #42b983;
}
.prism-player[data-v-2a4fcac3]{
  height:700px;
}
.ali-player[data-v-2a4fcac3]{
 width:100%;
 height:100%;
 position:absolute;
 top:0;
 left:0;
}
.prism-player[data-v-2a4fcac3]{
  width:100%!important;
  height:100%!important;
  position:absolute;
  top:0;
  left:0;
}
.prism-controlbar[data-v-2a4fcac3]{
  display:none!important;
}

.prism-big-play-btn{
  left:40%!important;
  bottom:30%!important;
}
.prism-player .prism-controlbar{
  z-index:-100!important;
}


#answer{
    width:100%;
    height:35%;
    position:absolute;
    background:#fefefe;
}
#answer:after{
    width: 100%;
    height: 60px;
    position: absolute;
    left: 0;
    top:100%;
    z-index: 0;
    content: '';
    border-radius: 0 0 50% 50%;
    background:#fff;
}
#answer .top{
    width:100%;
    height:50px;
    line-height:50px;
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack:distribute;
        justify-content:space-around;
}
#answer .top i{
    width:40px;
    height:40px;
    line-height:40px;
    margin-top:10px;
    background:#999;
    border-radius:50%;
}
#answer h3{
    width:80%;
    margin:10px auto;
}
#answer ul{
    width:80%;
}
#answer ul li{
     height:30px;
     line-height:30px;
     text-align:left;
     margin:10px 0;
     padding:0 20px;
     list-style:none;
     border:1px solid #999;
     border-radius:20px;
}
#answer ul li.true{
    color:#fff;
    background:green;
}
#answer ul li.false{
    color:#fff;
    background:red;
}
